// 
import React from 'react'
import './css/login.less'
import {Input, Button, Checkbox, message,Steps} from "antd";


import {withRouter}  from 'react-router-dom'
import {connect} from 'react-redux'
import MyFooter from "./components/footer/myFooter";
import http from './common/http'
import {changeLoginStatus, changeUser, insertTag} from "./store/action";

const { Step } = Steps;
const {Password} = Input;
class Login extends React.Component{

    state = {
        formType:0,                 //0  || 1  || 2 || 3
        loginLoading:false,         //loading
        username:'',                //
        password:'',                //
        phoneNum:'',                //
        phoneCode:'',               //
        imgSrc:'',                  //
        loginWrong:false,           //
        isAutoLogin:false,          //
        phoneCodeLoading:false,     //loading
        codeText:''         //

    }

    //1
    componentDidMount() {
        changeLoginStatus(1);
        // this.isHavenAuthenticated();
    }

    //
    componentWillUnmount() {
        const {loginStatus,changeLoginStatus} = this.props;
        if (loginStatus === 1){
            changeLoginStatus(0)
        }
    }

    //
    isHavenAuthenticated = ()=>{
        http.authenticatePost('getTicket').then(res=>{
            console.log(res)
            if (res.data.status === 200){
                http.get('login',{
                    ticket:res.data.data
                }).then(loginRes=>{
                    console.log(loginRes)
                    const {history} = this.props
                    this.setState({
                        loginLoading:false
                    });
                    // let {avatar,userId,userName} = loginRes.data.data;
                    // changeUser({avatar,userId,userName});            //
                    changeLoginStatus(2);           //
                    message.success('');               //
                    sessionStorage.setItem('haveLogin','true');             //sessionStorage
                    history.push('/')        //
                })
            }else {
                return null
            }
        })
    }

    //
    login = ()=>{
        const {changeUser,changeLoginStatus,history} = this.props;
        const {username,password} = this.state
        this.setState({
            loginLoading:true
        });
        http.authenticatePost('login',{
            userName:username,
            password:password
        }).then(res=>{
            if (res.data.status === 200){
                http.get('login',{
                    ticket:res.data.data
                }).then(loginRes=>{
                    this.setState({
                        loginLoading:false
                    });
                    let {avatar,userId,userName} = loginRes.data.data;
                    changeUser({avatar,userId,userName});            //
                    changeLoginStatus(2);           //
                    message.success('');               //
                    sessionStorage.setItem('haveLogin','true');             //sessionStorage
                    history.push('/')        //
                })
            } else {
                message.error('');
                this.setState({
                    loginLoading:false,
                    loginWrong:true,
                    username:'',
                    password:''
                });
            }
        }).catch(()=>{
            message.error('');
            this.setState({
                loginLoading:false,
                loginWrong:true,
                username:'',
                password:''
            });
        })
    };

    register = ()=>{
        //QQ
        const {username,password,phoneNum,phoneCode,imgSrc} = this.state
        const {history} = this.props
        http.authenticateGet('logon',{
            userName:username,
            password,
            phoneNum,
            mail:'',
            role:1,
            code:phoneCode,
            avatar:imgSrc
        }).then(res=>{
            console.log(res)
            if (res.data.status === 200){
                message.success('')
                this.login()
            }else {
                this.setState({
                    loginLoading:false,
                    loginWrong:true,
                })
            }
        })

    }

    phoneLogin = ()=>{
        const {phoneNum,phoneCode} = this.state
        const {history} = this.props
        http.authenticateGet('loginByPhoneNum',{
            phoneNum,
            code:phoneCode
        }).then(res=>{
            if (res.data.status === 200){
                http.get('login',{
                    ticket:res.data.data
                }).then(loginRes=>{
                    this.setState({
                        loginLoading:false
                    });
                    let {avatar,userId,userName} = loginRes.data.data;
                    changeUser({avatar,userId,userName});            //
                    changeLoginStatus(2);           //
                    message.success('');               //
                    sessionStorage.setItem('haveLogin','true');             //sessionStorage
                    history.push('/')        //
                })
            } else {
                message.error(res.data.msg);
                this.setState({
                    loginLoading:false,
                    loginWrong:true,
                    username:'',
                    password:''
                });
            }
        }).catch(()=>{
            message.error('');
            this.setState({
                loginLoading:false,
                loginWrong:true,
                username:'',
                password:''
            });
        })
    }

    changeText = (e)=>{
        this.setState({[e.target.dataset.statename]:e.target.value})
    }

    cancelLoginWrong = ()=>{
        this.setState({loginWrong:false})
    };

    toggleIsAutoLogin = ()=>{
        const {isAutoLogin} = this.state;
        this.setState({isAutoLogin: !isAutoLogin})
    };

    changeFormType = (e)=>{
        const {formtype} = e.target.dataset
        this.setState({
            formType:+formtype,
            loginLoading:false,         //loading
            username:'',                //
            password:'',                //
            phoneNum:'',                //
            phoneCode:'',               //
            imgSrc:'',                  //
            loginWrong:false,           //
        })
    }

    handlePressEnter = ()=>{
        const {formType} = this.state;
        switch (formType) {
            case 0:this.login();break;
            case 1:this.register();break;
            case 2:this.phoneLogin();break;
        }
    }

    clickUpload = ()=>{
        document.getElementById('registerUploadLocalImg').click();
    }

    uploadAvatar = ()=>{
        let that = this;
        let file = document.getElementById('registerUploadLocalImg').files[0];

        if (!file || !window.FileReader) { // FileReader
            message.warn('FileReader');
            return
        }
        if (/^image/.test(file.type)) {
            let formData=new FormData();
            formData.append('file',file)
            http.updatePost('uploadFile',formData).then(res=>{
                this.setState({
                    imgSrc:res.data.data
                })
            })
        }
    }

    sendCode = ()=>{
        const {formType,phoneNum,phoneCodeLoading} = this.state;
        this.setState({
            phoneCodeLoading:true
        })
        let type = '';
        switch (formType) {
            case 1:type = 'logon';break;
            case 2:type = 'login';break;
            case 3:type = 'changePassword';break;
            default:break
        }
        if (phoneNum[0] === '1' && phoneNum.length === 11){
            http.authenticateGet('getPhoneCode',{
                phoneNum,
                type
            }).then(()=>{
                let time = 60
                this.setState({
                    phoneCodeLoading:false
                })
                let a = setInterval(()=>{
                    if (time > 0 ){
                        this.setState({
                            codeText:time-- +''
                        })
                    }else {
                        clearInterval(a)
                        this.setState({
                            codeText:''
                        })
                    }
                },1000)
            })
        }else {
            message.warn('')
        }
    }


    formContent = ()=>{
        const {formType,isLogin,loginWrong,password,username,phoneNum,phoneCode,isAutoLogin,loginLoading,imgSrc,codeText,phoneCodeLoading} = this.state;
        const Username = <Input className={'input_wp'}
                                data-statename={'username'}
                                size={'large'}
                                placeholder=""
                                value={username}
                                name={'username'}
                                onChange={this.changeText}
                                onFocus={this.cancelLoginWrong}
                                onPressEnter={this.handlePressEnter}
                                prefix={<i className={'iconfont iconloginusername inputIcon'}/>}/>;
        const Password = <Input className={'input_wp'}
                                data-statename={'password'}
                                size={'large'}
                                placeholder=""
                                name={'password'}
                                value={password}
                                onChange={this.changeText}
                                onFocus={this.cancelLoginWrong}
                                onPressEnter={this.handlePressEnter}
                                prefix={<i className={'iconfont iconloginpassword inputIcon'}/>}/>;
        const PhoneNum = <Input className={'input_wp'}
                             data-statename={'phoneNum'}
                             size={'large'}
                             placeholder=""
                             value={phoneNum}
                             name={'phoneNum'}
                             onChange={this.changeText}
                             onFocus={this.cancelLoginWrong}
                             prefix={<i className={'iconfont iconiphone7 inputIcon'} style={{fontSize:'20px'}}/>}/>;
        const PhoneCode = <div className={'spaceBetween'} style={{margin:'0'}}>
                                <Input className={'input_wp'}
                                       data-statename={'phoneCode'}
                                       size={'large'}
                                       placeholder=""
                                       value={phoneCode}
                                       name={'phoneCode'}
                                       onChange={this.changeText}
                                       onFocus={this.cancelLoginWrong}
                                       onPressEnter={this.handlePressEnter}/>
                                <Button size={'large'} style={{marginLeft:20}} loading={phoneCodeLoading} onClick={this.sendCode} disabled={codeText !== ''}>{codeText}</Button>
                          </div>;


        //0  || 1  || 2 || 3
        switch (formType) {
            case 0:return (
                    <>
                        {Username}
                        {Password}
                        <div className={'spaceBetween'}>
                            <div className={'autoLogin'}>
                                <Checkbox checked={isAutoLogin} onChange={this.toggleIsAutoLogin}/>
                                <span onClick={this.toggleIsAutoLogin}></span>
                            </div>
                            <a href={'#'} data-formtype={3} onClick={this.changeFormType}></a>
                        </div>
                        <Button type={'primary'} size={'large'} block loading={loginLoading} onClick={this.login}></Button>
                        <div className={'spaceBetween'}>
                            <div className={'otherWay'}>
                                
                                <i className={'iconfont iconiphone7'} data-formtype={2} onClick={this.changeFormType} title={''}/>
                                <i className={'iconfont iconqq'} title={'QQ'}/>
                            </div>
                            <a href={'#'} data-formtype={1} onClick={this.changeFormType}></a>
                        </div>
                    </>);
            case 1:return (
                <>
                    <div className={'avatar_wp'} onClick={this.clickUpload}>
                        <input type="file" hidden onChange={this.uploadAvatar} id={'registerUploadLocalImg'} accept="image/*"/>
                        {
                            imgSrc === ''
                            ?<i className={'iconfont iconadd'}/>
                            :<img src={imgSrc} alt={username}/>
                        }
                    </div>
                    {Username}
                    <span className={'tips'}>
                        <i className={'iconfont iconwarn'}/>
                        210
                    </span>
                    {Password}
                    <span className={'tips'}>
                        <i className={'iconfont iconwarn'}/>
                        616
                    </span>
                    {PhoneNum}
                    {PhoneCode}
                    <div className={'spaceBetween'}>
                        <Button type={'primary'} style={{width:120}} size={'large'} onClick={this.register}></Button>
                        <a href={"#"} data-formtype={0} onClick={this.changeFormType}></a>
                    </div>
                </>
            );
            case 2:return (
                    <>
                        {PhoneNum}
                        {PhoneCode}
                        <Button type={'primary'} size={'large'} block loading={loginLoading} style={{marginTop:'10px'}} onClick={this.phoneLogin}></Button>
                        <div className={'spaceBetween'}>
                            <div className={'otherWay'}>
                                
                                <i className={'iconfont iconloginusername'} style={{fontSize:18}} data-formtype={0} onClick={this.changeFormType} title={''}/>
                                <i className={'iconfont iconqq'} title={'QQ'}/>
                            </div>
                            <a href={'#'} data-formtype={1} onClick={this.changeFormType}></a>
                        </div>
                    </>)
            case 3:return (
                <>
                    {PhoneNum}
                    {PhoneCode}
                    {Password}
                    <span className={'tips'}>
                        <i className={'iconfont iconwarn'}/>
                        616
                    </span>
                    <div className={'spaceBetween'}>
                        <Button type={'primary'} style={{width:120}} size={'large'} onClick={this.register}></Button>
                        <a href={"#"} style={{color:'red'}} data-formtype={0} onClick={this.changeFormType}></a>
                    </div>
                </>
            )

        }
    };

    render() {
        const {history} = this.props;
        const {formType,isLogin,loginWrong,password,username,isAutoLogin,loginLoading,phoneNum} = this.state;
        return (
            <div className={'login'}>
                <div className={`login_wp ${loginWrong?'wrong':''}`}>
                    {this.formContent()}
                </div>
                <MyFooter/>
            </div>
        )
    }

}

function mapStateToProps(state) {
    const {loginStatus} = state;
    return {loginStatus}
}

function mapDispatchToProps() {
    return{
        changeLoginStatus,
        changeUser
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Login));
