import React,{PureComponent} from 'react'

import {insertTag} from "../../../../store/action";
import {Menu, Dropdown, message, Tooltip} from 'antd';
import store from '../../../../store'
import {connect} from 'react-redux'



import '../css/insert.less'
import {TableModal} from "../../common/modals/tableModal";
import {ImageModal} from "../../common/modals/imageModal";
import Mask from "../mask";
import {getObjByKeyFromTagList} from "../../../../common/units";

class insert extends PureComponent{

    state = {
        showTableModal:false,       //
        showImageModal:false,       //
        imageModalTitle:'',         //
    }

    //
    formatTag = (type,name)=>{
        const {selectedTag,tagList} = this.props
        let parent = getObjByKeyFromTagList(selectedTag.pid,tagList);
        return {
            type:type,
            pid:selectedTag.pid,
            key:`${selectedTag.pid}-${parent.willCreateKey}`,
            dataName:name,
            iconName:'icon'+type,
            content:`${type}`,
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:0,
            props:{},
            children:[]
        }
    };

    //
    formatImage = (name,src) =>{
        const {selectedTag,tagList} = this.props
        let parent = getObjByKeyFromTagList(selectedTag.pid,tagList);
        this.setState({
            showImageModal:false
        })
        return {
            type:'img',
            pid:selectedTag.pid,
            key:`${selectedTag.pid}-${parent.willCreateKey}`,
            dataName:name,
            iconName:'iconimg',
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:0,
            props:{
                src
            }
        }
    };

    //
    formatTable = (arr,className)=>{
        const {selectedTag,tagList} = this.props
        let parent = getObjByKeyFromTagList(selectedTag.pid,tagList);
        this.setState({
            showTableModal:false
        });

        //tr
        let trs = new Array(arr.length);
        arr.forEach((item,index)=>{
           trs[index] = Object.assign({},{
               type:'tr',
               pid:`${selectedTag.pid}-${parent.willCreateKey}-0`,
               key:`${selectedTag.pid}-${parent.willCreateKey}-0-${index}`,
               dataName:`tr${index+1}`,
               iconName:'icontr',
               trueStyle:{},
               viewStyle:{},
               hoverTrueStyle:{},
               hoverViewStyle:{},
               willCreateKey:arr.length === 0 ? 0 :arr.length+1,
               props:{},
               children:[]
            })
        });

        //trchildrenth
        arr.forEach((item,index)=>{
            trs[0].children.push({
                type:'th',
                pid:trs[0].key,
                key:`${trs[0].key}-${index}`,
                dataName:`${index+1}${item}`,
                iconName:'iconth',
                content:item,
                trueStyle:{},
                viewStyle:{},
                hoverTrueStyle:{},
                hoverViewStyle:{},
                willCreateKey:0,
                props:{},
                children:[]
            })
        });


        //trtd
        for (let i=1;i<trs.length;i++){
            trs[i].children.push(...arr.map((item,index)=>{
                return {
                    type:'td',
                    pid:trs[i].key,
                    key:`${trs[i].key}-${index}`,
                    dataName:`${arr.length*i+index}`,
                    iconName:'icontd',
                    content:`${arr.length*i+index}`,
                    trueStyle:{},
                    viewStyle:{},
                    hoverTrueStyle:{},
                    hoverViewStyle:{},
                    willCreateKey:0,
                    props:{},
                    children:[]
                }
            }))
        }

        return {
            type:'table',
            pid:selectedTag.pid,
            key:`${selectedTag.pid}-${parent.willCreateKey}`,
            dataName:'table',
            iconName:'icontable',
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:1,
            props:{
                className:[className]
            },
            children: [
                {
                    type:'tbody',
                    pid:`${selectedTag.pid}-${parent.willCreateKey}`,
                    key:`${selectedTag.pid}-${parent.willCreateKey}`+'-0',
                    dataName:'tbody',
                    iconName:'icontbody',
                    trueStyle:{},
                    viewStyle:{},
                    hoverTrueStyle:{},
                    hoverViewStyle:{},
                    willCreateKey:trs.length === 0 ? 0 : trs.length+1,
                    props:{},
                    children:trs
                }
            ]
        }
    };

    formatInput = (type)=>{
        const {selectedTag} = this.props
        let dataName = '';
        let iconName = '';
        switch (type) {
            case 'text':dataName = 'text';iconName = 'iconinput';break;
            case 'password':dataName = 'password';iconName = 'iconpassword';break;
            case 'number':dataName = 'number';iconName = 'iconnumber';break;
            case 'checkbox':dataName = '';iconName = 'iconcheckbox';break;
            case 'radio':dataName = '';iconName = 'iconradio';break;
            default:dataName = 'text';iconName = 'iconinput';
        }
        return {
            type:'input',
            pid:selectedTag.pid,
            key:`${selectedTag.pid}-${selectedTag.willCreateKey}`,
            dataName,
            iconName,
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:0,
            props:{
                type
            }
        }
    }


    //jsinput
    clickUpload = ()=>{
        document.getElementById('uploadLocalImg').click();
    };

    //
    handleUploadImage = ()=>{
        let that = this;
        let file = document.getElementById('uploadLocalImg').files[0];
        if (!file || !window.FileReader) { // FileReader
            message.warn('FileReader');
            return
        }
        if (/^image/.test(file.type)) {
            let reader = new FileReader(); // reader
            reader.readAsDataURL(file); //  base64 
            reader.onloadend = function () { // 
                message.success('');
                insertTag(that.formatImage(file.name,this.result))
            }
        }
    };


    render() {
        const {showTableModal,showImageModal,imageModalTitle} = this.state
        const {insertTag,selectedTag} = this.props

        //  
        const textMenu = (
            <Menu>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('p','p'))}}>
                    <i className={'iconfont iconp'}/>
                    <span>p</span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('a','a'))}}>
                    <i className={'iconfont icona'}/>
                    <span>a</span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('pre','pre'))}}>
                    <i className={'iconfont iconpre'}/>
                    <span>pre</span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('code','code'))}}>
                    <i className={'iconfont iconcode'}/>
                    <span>code</span>
                </Menu.Item>
            </Menu>
        );

        //  
        const hMenu = (
            <Menu>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('h2','h2'))}}>
                    <i className={'iconfont iconh2'}/>
                    <span></span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('h3','h3'))}}>
                    <i className={'iconfont iconh3'}/>
                    <span></span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('h4','h4'))}}>
                    <i className={'iconfont iconh4'}/>
                    <span></span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('h5','h5'))}}>
                    <i className={'iconfont iconh5'}/>
                    <span></span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>{insertTag(this.formatTag('h6','h6'))}}>
                    <i className={'iconfont iconh6'}/>
                    <span></span>
                </Menu.Item>
            </Menu>
        )

        //   
        const imgMenu = (
            <Menu>
                <Menu.Item className={'menuItem'} onClick={()=>this.setState({showImageModal:true,imageModalTitle:''})}>
                    <i className={'iconfont iconnetworkimg'}/>
                    <span></span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>this.setState({showImageModal:true,imageModalTitle:'base64'})}>
                    <i className={'iconfont iconzip'}/>
                    <span>base64</span>
                </Menu.Item>
            </Menu>
        );

        // //   
        // const imgMenu = (
        //     <div className={'tag_wp popover'}>
        //         <div className={'tag'} onClick={()=>{
        //             this.setState({
        //                 showImageModal:true,
        //                 imageModalTitle:''
        //             })
        //         }}>
        //             <i className={'iconfont iconnetworkimg'}/>
        //             <p className={'tagName'}></p>
        //         </div>
        //         <div className={'tag'} onClick={()=>{
        //             this.setState({
        //                 showImageModal:true,
        //                 imageModalTitle:'base64'
        //             })
        //         }}>
        //             <i className={'iconfont iconzip'}/>
        //             <p className={'tagName'}>base64</p>
        //         </div>
        //     </div>
        // );

        //   
        const inputMenu = (
            <Menu>
                <Menu.Item className={'menuItem'} onClick={()=>insertTag(this.formatInput('password'))}>
                    <i className={'iconfont iconpassword'}/>
                    <span></span>
                </Menu.Item>
                <Menu.Item className={'menuItem'} onClick={()=>insertTag(this.formatInput('number'))}>
                    <i className={'iconfont iconnumber'}/>
                    <span></span>
                </Menu.Item>
            </Menu>
        );

        return (
            <div>
                <div style={{position:'relative',width:'max-content'}}>
                    {
                        JSON.stringify(selectedTag)==='{}'?<Mask title={''}/>:<></>
                    }
                    <div className={'tag_wp'}>
                        <div className={'tag'} onClick={()=>{insertTag(this.formatTag('div','div'))}}>
                            <i className={'iconfont icondiv'}/>
                            <p className={'tagName'}>div</p>
                        </div>
                        <div className={'tag'}>
                            <div onClick={()=>{insertTag(this.formatTag('button','button'))}}>
                                <i className={'iconfont iconbutton'}/>
                                <p className={'tagName'}></p>
                            </div>
                        </div>
                        <div className={'tag'}>
                            <div onClick={()=>{insertTag(this.formatTag('span','span'))}}>
                                <i className={'iconfont icontext'}/>
                                <p className={'tagName'}></p>
                            </div>
                            <Dropdown overlay={textMenu} placement="bottomLeft" trigger={['click']}>
                                <div className={'arrow'}>
                                    <i className={'iconfont icondownarrow'}/>
                                </div>
                            </Dropdown>
                        </div>
                        <div className={'tag'}>
                            <div onClick={()=>{insertTag(this.formatTag('h1','h1'))}}>
                                <i className={'iconfont iconh1'}/>
                                <p className={'tagName'}></p>
                            </div>
                            <Dropdown overlay={hMenu} placement="bottomLeft" trigger={['click']}>
                                <div className={'arrow'}>
                                    <i className={'iconfont icondownarrow'}/>
                                </div>
                            </Dropdown>
                        </div>
                        <div className={'tag'}>
                            <div onClick={this.clickUpload}>
                                <i className={'iconfont iconimg'}/>
                                <p className={'tagName'}></p>
                            </div>
                            <input type="file" hidden onChange={this.handleUploadImage} id={'uploadLocalImg'} accept="image/*"/>
                            <Dropdown overlay={imgMenu} placement="bottomLeft" trigger={['click']}>
                                <div className={'arrow'}>
                                    <i className={'iconfont icondownarrow'}/>
                                </div>
                            </Dropdown>
                        </div>
                        <div className={'tag'} onClick={()=>this.setState({showTableModal:true})}>
                            <i className={'iconfont icontable'}/>
                            <p className={'tagName'}></p>
                        </div>
                        <div className={'tag'}>
                            <i className={'iconfont iconlist'}/>
                            <p className={'tagName'}></p>
                            <div className={'arrow'}>
                                <i className={'iconfont icondownarrow'}/>
                            </div>
                        </div>
                        <div className={'tag'}>
                            <i className={'iconfont iconpseudoelement'}/>
                            <p className={'tagName'}></p>
                            <div className={'arrow'}>
                                <i className={'iconfont icondownarrow'}/>
                            </div>
                        </div>
                        <div className={'tag line'}>
                            <i className={'iconfont iconfavorite'}/>
                            <p className={'tagName'}></p>
                            <div className={'arrow'}>
                                <i className={'iconfont icondownarrow'}/>
                            </div>
                        </div>

                        <div className={'tag'}>
                            <div onClick={()=>insertTag(this.formatInput('text'))}>
                                <i className={'iconfont iconinput'}/>
                                <p className={'tagName'}></p>
                            </div>
                            <Dropdown overlay={inputMenu} placement="bottomLeft" trigger={['click']}>
                                <div className={'arrow'}>
                                    <i className={'iconfont icondownarrow'}/>
                                </div>
                            </Dropdown>
                        </div>
                        <div className={'tag'} onClick={()=>insertTag(this.formatInput('checkbox'))}>
                            <i className={'iconfont iconcheckbox'}/>
                            <p className={'tagName'}></p>
                        </div>
                        <div className={'tag'} onClick={()=>insertTag(this.formatInput('radio'))}>
                            <i className={'iconfont iconradio'}/>
                            <p className={'tagName'}></p>
                        </div>

                    </div>
                <TableModal type={''} showTableModal={showTableModal} ok={(arr,className)=>insertTag(this.formatTable(arr,className))} cancel={()=>this.setState({showTableModal:false})}/>
                <ImageModal type={''} showImageModal={showImageModal} ok={(name,src)=>insertTag(this.formatImage(name,src))} cancel={()=>this.setState({showImageModal:false})} imageModalTitle={imageModalTitle}/>
            </div>
            </div>
        )
    }

}

function mapStateToProps(state) {
    const {selectedTag,tagList} = state;
    return {selectedTag,tagList}
}

function mapDispatchToProps() {
    return {
        insertTag
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(insert)
