import React,{PureComponent} from 'react'
import {Modal,Input,message} from "antd";
import http from '../../../../common/http'
import '../css/inviteModal.less'
import '../css/modals.less'
const { Search } = Input;

class InviteModal extends PureComponent{

    state = {
        searchText:'',
        isInvite:true,
        userInfo:{},
        okText:''
    }

    searchUser = (value)=>{
        http.get('/getUserInfoByUserName',{userName:value}).then((res)=>{
            let {userName,avatar,userId} = res.data.data;
            this.setState({
                userInfo:{
                    userName,
                    avatar,
                    userId
                }
            })
            // if (res.msg !== ''){
                this.setState({
                    isInvite:false,
                    okText:`${this.state.userInfo.userName}`
                })
            // }else {
            //     this.setState({
            //         okText:''
            //     })
            // }
        })
    }

    cancel = ()=>{
        const {cancel} = this.props
        cancel();
        this.setState({
            searchText:'',
            isInvite:true,
            userInfo:{}
        })
    }

    ok = ()=>{
        const {fileId} = this.props
        const {userInfo} = this.state
        http.get('/addNohtmlProjectPartner',{
            projectId:fileId,
            userId:userInfo.userId
        }).then(res=>{
            if (res.data.msg === ''){
                message.warn('');
            }else {
                message.success('');
                this.cancel();
            }

        })

    }

    render() {
        const {fileName,showInviteModal} = this.props
        const {okText,searchText,userInfo} = this.state
        return (
            <Modal title={''} width={1000}
                   className={'inviteModal modals'}
                   visible={showInviteModal}
                   cancelText={''}
                   okText={okText}
                   // okButtonProps={{disabled:this.state.isInvite}}
                   onOk={this.ok}
                   onCancel={this.cancel}>
                <div>
                    <div className={'empty'}></div>
                    <input type="text"/>
                    {/*{*/}
                    {/*    JSON.stringify(userInfo) === '{}'*/}
                    {/*        ?<div className={'empty'}></div>*/}
                    {/*        :<div className={'userInfo'}>*/}
                    {/*            <div>*/}
                    {/*                <img src={userInfo.avatar} alt={userInfo.userId}/>*/}
                    {/*            </div>*/}
                    {/*            <p>{userInfo.userName}</p>*/}
                    {/*        </div>*/}
                    {/*}*/}
                </div>
            </Modal>
        )
    }

}

export default InviteModal
