import React,{PureComponent} from 'react'
import {connect} from 'react-redux'
import {Tree, Popover, Input, message,Popconfirm} from 'antd';
import './MySider.less'
import {
    addTag,
    changeCurrentTag,
    updateTag,
    changeDrawer,
    changeHoveredTag,
    deleteTag,
    dropTag,
    reSetTag
} from "../../../store/action";
import {TableModal} from "../common/modals/tableModal";
import {ImageModal} from "../common/modals/imageModal";
import {UploadFile} from "../../../common/api";

const { TreeNode } = Tree;

class MySider extends PureComponent{

    state = {
        display:'none',         //displaynoneblock
        rightClickNodeTreeItem: {         //
            pageX: '',
            pageY: ''
        },
        reNameVisible:false,        //
        showTableModal:false,
        showImageModal:false,       //
        imageModalTitle:'',         //
    }

    //
    stopDefault = (e)=>{
        e.cancelBubble=true;
        e.stopPropagation();
    };

    // 
    formatTag = (type,name)=>{
        const {selectedTag} = this.props

        //TODO willCreateKey

        return {
            type:type,
            pid:selectedTag.key,
            key:`${selectedTag.key}-${selectedTag.willCreateKey}`,
            dataName:name,
            iconName:'icon'+type,
            content:`${type}`,
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:0,
            props:{},
            children:[]
        }
    };

    formatImage = (name,src) =>{
        const {selectedTag} = this.props
        this.setState({
            showImageModal:false
        });
        return {
            type:'img',
            pid:selectedTag.key,
            key:`${selectedTag.key}-${selectedTag.willCreateKey}`,
            dataName:name,
            iconName:'iconimg',
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:0,
            props:{
                src
            }
        }
    };

    formatTable = (arr,className)=>{
        const {selectedTag} = this.props
        this.setState({
            showTableModal:false
        });

        //TODO trthtd willCreateKeywillCreateKeytable

        //tr
        let trs = new Array(arr.length);
        arr.forEach((item,index)=>{
            trs[index] = Object.assign({},{
                type:'tr',
                pid:`${selectedTag.key}-${selectedTag.willCreateKey}-0`,
                key:`${selectedTag.key}-${selectedTag.willCreateKey}-0-${index}`,
                dataName:`tr${index+1}`,
                iconName:'icontr',
                trueStyle:{},
                viewStyle:{},
                hoverTrueStyle:{},
                hoverViewStyle:{},
                willCreateKey:arr.length === 0 ? 0 :arr.length+1,
                props:{},
                children:[]
            })
        });

        //trchildrenth
        arr.forEach((item,index)=>{
            trs[0].children.push({
                type:'th',
                pid:trs[0].key,
                key:`${trs[0].key}-${index}`,
                dataName:`${index+1}${item}`,
                iconName:'iconth',
                content:item,
                trueStyle:{},
                viewStyle:{},
                hoverTrueStyle:{},
                hoverViewStyle:{},
                willCreateKey:0,
                props:{},
                children:[]
            })
        });


        //trtd
        for (let i=1;i<trs.length;i++){
            trs[i].children.push(...arr.map((item,index)=>{
                return {
                    type:'td',
                    pid:trs[i].key,
                    key:`${trs[i].key}-${index}`,
                    dataName:`${arr.length*i+index}`,
                    iconName:'icontd',
                    content:`${arr.length*i+index}`,
                    trueStyle:{},
                    viewStyle:{},
                    hoverTrueStyle:{},
                    hoverViewStyle:{},
                    willCreateKey:0,
                    props:{},
                    children:[]
                }
            }))
        }

        return {
            type:'table',
            pid:selectedTag.key,
            key:`${selectedTag.key}-${selectedTag.willCreateKey}`,
            dataName:'table',
            iconName:'icontable',
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:1,
            props:{
                className:[className]
            },
            children: [
                {
                    type:'tbody',
                    pid:`${selectedTag.key}-${selectedTag.willCreateKey}`,
                    key:`${selectedTag.key}-${selectedTag.willCreateKey}-0`,
                    dataName:'tbody',
                    iconName:'icontbody',
                    trueStyle:{},
                    viewStyle:{},
                    hoverTrueStyle:{},
                    hoverViewStyle:{},
                    willCreateKey:trs.length === 0 ? 0 : trs.length+1,
                    props:{},
                    children:trs
                }
            ]
        }
    };

    formatInput = (type)=>{
        const {selectedTag} = this.props
        let dataName = '';
        let iconName = '';
        switch (type) {
            case 'text':dataName = 'text';iconName = 'iconinput';break;
            case 'password':dataName = 'password';iconName = 'iconpassword';break;
            case 'number':dataName = 'number';iconName = 'iconnumber';break;
            case 'checkbox':dataName = '';iconName = 'iconcheckbox';break;
            case 'radio':dataName = '';iconName = 'iconradio';break;
            default:dataName = 'text';iconName = 'iconinput';
        }
        return {
            type:'input',
            pid:selectedTag.key,
            key:`${selectedTag.key}-${selectedTag.willCreateKey}`,
            dataName,
            iconName,
            trueStyle:{},
            viewStyle:{},
            hoverTrueStyle:{},
            hoverViewStyle:{},
            willCreateKey:0,
            props:{
                type
            }
        }
    };

    //tree
    treeNodeonClick = e =>{
        const {tagList,showDrawer,changeCurrentTag,changeHoveredTag,changeDrawer} = this.props
        if(e.toString()){
            //TODO 
            // changeCurrentTag(e.toString());
            changeCurrentTag(e.toString(),tagList);
        }
        if (!showDrawer){
            //hoverTag
            changeHoveredTag('');
            changeDrawer(true);
            // key
            // setTimeout(()=>changeHoveredTag(e.toString()),1000);        //bug
        }

    };

    // tree
    treeNodeonRightClick = e => {
        const {tagList,changeCurrentTag} = this.props
        e.node.onSelect(e.event);       //
        //TODO 
        // changeCurrentTag(e.node.props.eventKey);
        changeCurrentTag(e.node.props.eventKey,tagList);
        this.setState({
            display: 'block',
            rightClickNodeTreeItem: {
                pageX: e.event.pageX,
                pageY: e.event.pageY
            },
        });
    };

    // 
    hideRight = () => {
        this.setState({
            display: 'none',
        });
    };

    //json
    createNodes = (val) => {
        const {selectedTag} = this.props
        // if (val.type==='img'){
        //     console.log(val)
        // }
        let son = null;
        if (val.children){
            son = [...val.children.map(val => this.createNodes(val))];
        }
        return <TreeNode className={`${selectedTag.key===val.key}?'ant-tree-node-selected':''`} icon={<i className={`iconfont ${val.iconName}`} onClick={(e)=>this.treeNodeonClick(e)}/>} title={val.dataName} key={val.key}>{son}</TreeNode>
        // return <TreeNode className={`${this.state.selectedTag.key===val.key}?'ant-tree-node-selected':''`} icon={<i className={`iconfont ${val.iconName}`} onClick={(e)=>this.treeNodeonClick(e)}/>} title={val.key} key={val.key}>{son}</TreeNode>
    };


    handleVisibleChange = reNameVisible => {
        this.setState({ reNameVisible });
    };

    //jsinput
    clickUpload = ()=>{
        document.getElementById('uploadLocalImg_Sider').click();
    };

    //
    handleUploadImage = ()=>{

        let form = document.getElementById('upload_form');
        // window.event.preventDefault();
        // console.log('aaaaaaaaaa')
        // let that = this;
        // let file = document.getElementById('uploadLocalImg_Sider').files[0];        //input filefile
        // console.log(file);
        // console.log(UploadFile(file));

        let FD = new FormData(form);
        console.log(FD.get('file'))
        console.log(FD)
        // FD.append('file',file);
        UploadFile(FD)


        /*FileReaderbase64*/

        // if (!file || !window.FileReader) { // FileReader
        //     message.warn('FileReader');
        //     return
        // }
        // if (/^image/.test(file.type)) {
        //     let reader = new FileReader(); // reader
        //     // reader.readAsDataURL(file); //  base64 
        //     reader.readAsText(file,'utf-8'); //  base64 
        //     reader.onloadend = function () { // 
        //         message.success('');
        //         console.log(this.result)
        //         console.log(UploadFile(this.result));
        //         addTag(that.formatImage(file.name,this.result))
        //
        //     }
        // }
    };

    //
    reName = (e)=>{
        const {updateTag} = this.props
        updateTag({
            prop:'dataName',
            value:e.target.value
        })
    };

    reNameTitle = ()=>{
        const {selectedTag} = this.props
        return (
            <div className={'reNameTitle'}>
                <span className={'title'}>{selectedTag.dataName}</span>
                <span className={'cancel'} onClick={()=>{this.setState({reNameVisible:false})}}></span>
            </div>
        )
    };

    //
     reNameContent = ()=>{
         const {selectedTag} = this.props
         return (
             <div>
                 <Input value={selectedTag.dataName} onChange={this.reName} onClick={(e)=>this.stopDefault(e)}/>
             </div>
         );
     };

     drop = (e)=>{
         const {reSetTag} = this.props
         reSetTag();
         if(e.node.props.eventKey === '0' && !e.node.props.dragOver){
             message.error('');
         }else {
             dropTag({
                 dropOver:e.node.props.dragOver,
                 originKey:e.dragNodesKeys,
                 targetKey:e.node.props.eventKey,
                 dropPosition:e.node.props.dragOverGapTop ? 'top':'bottom',
             })
         }

     };

    // 
    getNodeTreeRightClickMenu = () => {
        const {rightClickNodeTreeItem,display,reNameVisible} = this.state
        const {selectedTag,addTag,deleteTag} = this.props
        const { pageX, pageY } = { ...rightClickNodeTreeItem };
        const wp_style = {display};
        const menu_Style = {
            left: `${pageX }px`,
            top: `${pageY}px`,
            display: display,
        };
        const menu = (
            <div className={'contextMenu_wp'} style={wp_style} onClick={()=>this.hideRight()}>
                <ul className={'contextMenu'} style={menu_Style}>
                    <li className={`${selectedTag.children?'':'disable'}`}>
                        <i className={'iconfont iconnew'}/>
                        <p></p>
                        <i className={'iconfont iconrightarrow'}/>
                        <ul className={'contextMenu next'}>
                            <li onClick={()=>{addTag(this.formatTag('div','div'))}}>
                                <i className={'iconfont icondiv'}/>
                                <p>div</p>
                            </li>
                            <li onClick={()=>{addTag(this.formatTag('button','button'))}}>
                                <i className={'iconfont iconbutton'}/>
                                <p></p>
                            </li>
                            <li>
                                <i className={'iconfont icontext'}/>
                                <p></p>
                                <i className={'iconfont iconrightarrow'}/>
                                <ul className={'contextMenu next'}>
                                    <li onClick={()=>{addTag(this.formatTag('span','span'))}}>
                                        <i className={'iconfont iconspan'}/>
                                        <p>span</p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('p','p'))}}>
                                        <i className={'iconfont iconp'}/>
                                        <p>p</p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('a','a'))}}>
                                        <i className={'iconfont icona'}/>
                                        <p>a</p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('pre','pre'))}}>
                                        <i className={'iconfont iconpre'}/>
                                        <p>pre</p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('code','code'))}}>
                                        <i className={'iconfont iconcode'}/>
                                        <p>code</p>
                                    </li>
                                </ul>
                            </li>
                            <li>
                                <i className={'iconfont iconh'}/>
                                <p></p>
                                <i className={'iconfont iconrightarrow'}/>
                                <ul className={'contextMenu next'}>
                                    <li onClick={()=>{addTag(this.formatTag('h1','h1'))}}>
                                        <i className={'iconfont iconh1'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('h2','h2'))}}>
                                        <i className={'iconfont iconh2'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('h3','h3'))}}>
                                        <i className={'iconfont iconh3'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('h4','h4'))}}>
                                        <i className={'iconfont iconh4'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('h5','h5'))}}>
                                        <i className={'iconfont iconh5'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>{addTag(this.formatTag('h6','h6'))}}>
                                        <i className={'iconfont iconh6'}/>
                                        <p></p>
                                    </li>
                                </ul>
                            </li>
                            <li>
                                <i className={'iconfont iconimg'}/>
                                <p></p>
                                <i className={'iconfont iconrightarrow'}/>
                                <ul className={'contextMenu next'}>
                                    <li onClick={()=>this.clickUpload()}>
                                        <i className={'iconfont iconuploadimg'}/>
                                        <p></p>
                                        <form encType={'multipart/form-data'} id={'upload_form'}>
                                            <input type="file" hidden name={'file'} onChange={()=>this.handleUploadImage()} id={'uploadLocalImg_Sider'} accept="image/*"/>
                                        </form>

                                    </li>
                                    <li onClick={()=>this.setState({showImageModal:true,imageModalTitle:''})}>
                                        <i className={'iconfont iconnetworkimg'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>this.setState({showImageModal:true,imageModalTitle:'base64'})}>
                                        <i className={'iconfont iconzip'}/>
                                        <p>base64</p>
                                    </li>
                                </ul>
                            </li>
                            <li onClick={()=>this.setState({showTableModal:true})}>
                                <i className={'iconfont icontable'}/>
                                <p></p>
                            </li>
                            <li>
                                <i className={'iconfont iconlist'}/>
                                <p></p>
                            </li>
                            <li>
                                <i className={'iconfont iconcanvas'}/>
                                <p></p>
                            </li>
                            <li>
                                <i className={'iconfont iconfavorite'}/>
                                <p></p>
                            </li>
                            <li className={'line'}>
                                <div/>
                            </li>
                            <li>
                                <i className={'iconfont iconinput'}/>
                                <p></p>
                                <i className={'iconfont iconrightarrow'}/>
                                <ul className={'contextMenu next'}>
                                    <li onClick={()=>addTag(this.formatInput('text'))}>
                                        <i className={'iconfont iconinput'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>addTag(this.formatInput('password'))}>
                                        <i className={'iconfont iconpassword'}/>
                                        <p></p>
                                    </li>
                                    <li onClick={()=>addTag(this.formatInput('number'))}>
                                        <i className={'iconfont iconnumber'}/>
                                        <p></p>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </li>
                    <li className={'line'}>
                        <div/>
                    </li>
                    <li>
                        <i className={'iconfont iconcopy'}/>
                        <p></p>
                    </li>
                    <li>
                        <i className={'iconfont iconpaste'}/>
                        <p></p>
                    </li>
                    <li>
                        <i className={'iconfont iconcut'}/>
                        <p></p>
                    </li>
                    <li>
                        <Popconfirm
                            placement="rightTop"
                            title={'?'}
                            onConfirm={()=>deleteTag(this.state.selectedTag.key)}
                            onClick={(e)=>this.stopDefault(e)}
                            okText=""
                            cancelText=""
                        >
                            <div className={'reName_wp'}>
                                <i className={'iconfont icondelete'}/>
                                <p></p>
                            </div>
                        </Popconfirm>

                    </li>
                    <li className={'line'}>
                        <div/>
                    </li>
                    <li>
                        <i className={'iconfont iconaddfavorite'}/>
                        <p></p>
                    </li>
                    <li onClick={(e)=>this.stopDefault(e)}>
                        <Popover title={this.reNameTitle()} content={this.reNameContent()}
                                 visible={reNameVisible}
                                 trigger={'click'}
                                 placement="rightTop"
                                 onVisibleChange={this.handleVisibleChange}
                                 onClick={(e)=>this.stopDefault(e)}>
                            <div className={'reName_wp'}>
                                <i className={'iconfont iconrename'}/>
                                <p></p>
                            </div>
                        </Popover>,

                    </li>
                </ul>
            </div>
        );
        return rightClickNodeTreeItem == null ? '' : menu;
    };

    render() {
        const {changeHoveredTag,changeDrawer,tagList,addTag} = this.props
        const {showTableModal,showImageModal,imageModalTitle} = this.state
        return(
            <div style={{position:'relative'}}>
                <Tree showIcon defaultExpandAll draggable
                      onDragStart={()=>{                //hovermasklow
                          changeHoveredTag('');
                          changeDrawer(false)
                      }}
                      onDrop={(e)=>this.drop(e)}
                      onSelect={(e)=>this.treeNodeonClick(e)}
                      onMouseEnter={(e)=>{changeHoveredTag(e.node.props.eventKey)}}
                      onMouseLeave={()=>{changeHoveredTag('')}}
                      onRightClick={(e)=>this.treeNodeonRightClick(e)}>
                    <TreeNode icon={<i className={'iconfont icondiv'}/>} title='' key="0">
                        {tagList.children ? tagList.children.map(val=>this.createNodes(val)) : null}
                    </TreeNode>
                </Tree>
                {this.getNodeTreeRightClickMenu()}
                <TableModal type={''} showTableModal={showTableModal} ok={(arr,className)=>addTag(this.formatTable(arr,className))} cancel={()=>this.setState({showTableModal:false})}/>
                <ImageModal type={''} showImageModal={showImageModal} ok={(name,src)=>addTag(this.formatImage(name,src))} cancel={()=>this.setState({showImageModal:false})} imageModalTitle={imageModalTitle}/>
            </div>
        )
    }
}

function mapStateToProps(state) {
    const {selectedTag,tagList,showDrawer} = state;
    return {selectedTag,tagList,showDrawer}
}

function mapDispatchToProps() {
    return {
        addTag,
        changeCurrentTag,
        updateTag,
        changeDrawer,
        changeHoveredTag,
        deleteTag,
        dropTag,
        reSetTag
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(MySider);
