import React from 'react'

import './css/home.less'
import MyFooter from "./components/footer/myFooter";
import Swiper from 'swiper/js/swiper'
import 'swiper/css/swiper.css'

class Home extends React.Component{

    state = {
        mySwiper:null
    }

    componentDidMount() {
        let swiper = new Swiper('.swiper-container', {          //Swiper
            height:'100%',
            direction: 'horizontal',//
            loop: true,//
            parallax:true,
            speed:300,
            grabCursor : true,
            autoplay : {
                delay:3000,
                disableOnInteraction: false,
            },
            pagination: {
                el: '.swiper-pagination',
            },
            navigation: {//
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            }
        });
        this.setState({
            mySwiper:swiper
        })
    }

    componentWillUnmount() {
        const {mySwiper} = this.state;
        if (mySwiper !== null) { // swiper
            mySwiper.destroy()
        }
    }

    render() {
        return (
            <>
                <div className={'home_wp'}>
                    <div className="swiper-container">
                        <div className="swiper-wrapper">
                            <div className="swiper-slide">
                                <div className={'describe_wp'}>
                                    <div>
                                        <h3 className={'describe'}></h3>
                                        <pre className={'describe_content'}>
                                            DomWord
                                        </pre>
                                    </div>
                                    <img className={'screenShot'} src={require('./asset/mainBC_01.png')} alt={''}/>
                                </div>
                            </div>
                            <div className="swiper-slide">
                                <div className={'describe_wp'}>
                                    <img className={'screenShot'} src={require('./asset/mainBC_02.png')} alt={''}/>
                                    <div>
                                        <h3 className={'describe'}></h3>
                                        <pre className={'describe_content'}>
                                            @keyframes
                                        </pre>
                                    </div>

                                </div>
                            </div>
                            <div className="swiper-slide">Slide 3</div>
                        </div>
                    </div>

                    <div className="swiper-button-prev"/>
                    <div className="swiper-button-next"/>
                    <div className="swiper-pagination"/>
                </div>

                <MyFooter/>
            </>
        )
    }
}

export default Home
