import {
    changeAllKeyFrames,
    changeAllSetting,
    changeClassList,
    changeHoverStyle,
    changeNocssStyle, changeTagList
} from "../store/action";
import store from '../store'

/**
 * reduxreducerkeydom
 *
 * @param {String} key key
 * @param {Object} obj statetagList
 * */
export function getObjByKeyFromTagList(key,obj) {
    console.log(key)
    console.log(obj)
    if (obj.key === key){
        return obj;
    } else {
        for (let i=0; i<obj.children.length;i++){
            if (key.indexOf(obj.children[i].key) === 0){
                return getObjByKeyFromTagList(key,obj.children[i]);
            }
        }
    }
}

export function loadFile(list) {
    Object.entries(list).forEach(item=>{
        switch (item[0]) {
            case 'classList':changeClassList(item[1]);break;
            case 'keyframesList':changeAllKeyFrames(item[1]);break;
            case 'setting':changeAllSetting(item[1]);break;
            case 'tagList':{
                console.log(item)
                changeTagList(item[1]);
            }break;
            default:break;
        }
    })
}

/**
 * 
 *
 * @param {Object} originObj 
 * @param {String} styleType style'viewStyle''trueStyle''hoverTrueStyle''hoverViewStyle'
 *
 * @return {Object} css
 * */
export function getComputedCss(originObj,styleType) {
    let classStyle = {};
    let list = store.getState().classList.map(item=>item.className);
    if (JSON.stringify(originObj)!=='{}'){
        if (originObj.props.className){
            originObj.props.className.forEach((item)=>{
                if (list.includes(item)){
                    classStyle = Object.assign({},classStyle,store.getState().classList[list.indexOf(item)][styleType]);
                }
            });
        }


    }
    return Object.assign({},classStyle,originObj[styleType]);
}


/**
 *
 * nocss
 * extra
 *
 * @param {String} stateName csshovercss
 * @param {String} prop 
 * @param {String} value 
 * @param {Array} extra 
 *                      border:solid 2px black;
 *                      box-shadow
 *
 * */
export function changeProp (stateName,prop,value,...extra){
    let patt = new RegExp(`(?<=(.+\\s{1}){${extra[0]}})\\S+(?=\\s{1})`);           //
    switch (prop) {
        case 'color':
        case 'backgroundColor':
            switch (stateName) {
                case 'nocssStyle':{
                    changeNocssStyle({
                        prop,
                        value:colorRgba(value.color,value.alpha)
                    });break;
                }
                case 'hoverStyle':{
                    changeHoverStyle({
                        prop,
                        value:colorRgba(value.color,value.alpha)
                    });break;
                }
                default:return;
            }
            break;
        case 'fontSize':
            switch (stateName) {
                case 'nocssStyle':{
                    changeNocssStyle({
                        prop,
                        value:`${value}px`
                    });break;
                }
                case 'hoverStyle':{
                    changeHoverStyle({
                        prop,
                        value:`${value}px`
                    });break;
                }
                default:return;
            }
            break;
        case 'border':
        case 'padding':
        case 'borderRadius':
            switch (stateName) {
                case 'nocssStyle':{
                    let str = store.getState().nocssStyle[prop]+' ';
                    changeNocssStyle({
                        prop,
                        value:str.replace(patt,value).trim()
                    });
                    break;
                }
                case 'hoverStyle':{
                    let str = store.getState().hoverStyle[prop]+' ';
                    changeHoverStyle({
                        prop,
                        value:str.replace(patt,value).trim()
                    });
                    break;
                }
                default:return;
            }
            break;
        case 'boxShadow':
        case 'textShadow':

            switch (stateName) {
                case 'nocssStyle':{
                    let origin = store.getState().nocssStyle[prop].split(/(?<=rgba\(.*\)|#\w{6}),/g);
                    let temp = origin[extra[1]]+' ';
                    origin[extra[1]] = temp.replace(patt,value).trim();
                    changeNocssStyle({
                        prop,
                        value:origin.join(',')
                    });
                    break;
                }
                case 'hoverStyle':{
                    let origin = store.getState().hoverStyle[prop].split(/(?<=rgba\(.*\)|#\w{6}),/g);
                    let temp = origin[extra[1]]+' ';
                    origin[extra[1]] = temp.replace(patt,value).trim();
                    changeHoverStyle({
                        prop,
                        value:origin.join(',')
                    });
                    break;
                }
                default:return;
            }
            break;
        default:
            switch (stateName) {
                case 'nocssStyle':changeNocssStyle({prop,value});break;
                case 'hoverStyle':changeHoverStyle({prop,value});break;
                default:return;
            }
    }
}

/**
 * 16rgba
 * @param {String} color 16
 * @param {String} opacity 
 *
 * @return {String} rbga1
 *
 * */
export function colorRgba(color,opacity) {
    if (opacity !== 100){
        // 16
        let reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
        // 
        color = color.toLowerCase();
        if (reg.test(color)) {
            // #fff => #ffffff
            if (color.length === 4) {
                let colorNew = "#";
                for (let i = 1; i < 4; i += 1) {
                    colorNew += color.slice(i, i + 1).concat(color.slice(i, i + 1));
                }
                color = colorNew;
            }
            // RGB
            let colorChange = [];
            for (let i = 1; i < 7; i += 2) {
                colorChange.push(parseInt("0x" + color.slice(i, i + 2)));
            }
            return `rgba(${colorChange.join(',')},${opacity/100})`;
        } else {
            return color;
        }
    } else {
        return color;
    }
}

const dec2twoDigitHex = dec => Number(dec).toString(16).replace(/(^.$)/, '0$1')

export const rgb2hex = ({ r, g, b }) => {
    return `#${dec2twoDigitHex(r)}${dec2twoDigitHex(g)}${dec2twoDigitHex(b)}`
}

/**
 *  cssboxShadowtextShadow
 *
 *  @param {String} stateName csshovercss
 *  @param {String} prop 
 * */
export function addProp(stateName,prop) {
    switch (prop) {
        case 'boxShadow':
            let boxShadowValue = store.getState()[stateName].boxShadow+',0px 0px 0px 0px #000000';
            switch (stateName) {
                case 'nocssStyle':
                    changeNocssStyle({
                        prop,
                        value:boxShadowValue
                    });break;
                case 'hoverStyle':
                    changeHoverStyle({
                        prop,
                        value:boxShadowValue
                    });break;
                default:return;
            }
            break;
        case 'textShadow':
            let textShadowValue = store.getState()[stateName].textShadow+',0px 0px 0px #000000';
            switch (stateName) {
                case 'nocssStyle':
                    changeNocssStyle({
                        prop,
                        value:textShadowValue
                    });break;
                case 'hoverStyle':
                    changeHoverStyle({
                        prop,
                        value:textShadowValue
                    });break;
                default:return;
            }
            break;
        default:return;
    }
}

/**
 *  cssboxShadowtextShadow
 *
 *  @param {String} stateName csshovercss
 *  @param {String} prop 
 *  @param {Number} index 
 * */
export function deleteProp (stateName,prop,index){
    let value = store.getState()[stateName][prop];
    switch (prop) {
        case 'boxShadow':
        case 'textShadow':
            value = value.split(/(?<=rgba\(.*\)|#\w{6}),/g);
            value.splice(index,1);
            break;
        default:return;
    }
    switch (stateName) {
        case 'nocssStyle':
            changeNocssStyle({
                prop,
                value:value.join(',')
            });break;
        case 'hoverStyle':
            changeHoverStyle({
                prop,
                value:value.join(',')
            });break;
        default:return;
    }
}

/**
 * 
 *
 * */
export function gcd(m,n){
    if (n === 0){
        return m;
    } else {
        return gcd(n,m%n);
    }
};

export function autoSelectAll(e){
    e.target.select()
}
